body {
  background-color: whitesmoke;
  font-weight: 300;
  font-family: "Roboto", "Helvetica", "Arial", "sans-serif";
}
h1, h2, h3, h4 {
  color: #392515;
  font-weight: 300;
}
.center-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.register-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 5vh 15vw;
}
.register-link {
  text-decoration: none;
  color: #ff9800;
  padding: 5vh 0;
}
.page-box {
  margin: 84px 0;
}
.select-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.select {
  width: 200px;
}
@media only screen and (min-width: 600px) {
  /* For tablet */
}
@media only screen and (min-width: 768px) {
  /* For laptop */
  .page-box {
    margin: 84px 24px;
  }
}